<template>
  <li>
    <div class="grid-container">
      <div
        class="grid-item grid-item-poster"
        style="grid-column: 1 / 2; grid-row: 1 / span 3"
      >
        <img :src="posterLink" style="width: 140px" />
      </div>
      <div class="grid-item grid-item-details" style="grid-column: 2 / span 4">
        <h4>{{ rowId }}. {{ fileName }}</h4>
        <hr />
        <h5>
          {{ isHd ? `HD ${size}` : `${size}` }}
          {{
            imdbDetail
              ? ` | Imdb: ${imdbDetail.imdbRating}  | Released: ${imdbDetail.Released}`
              : ""
          }}
        </h5>
        <hr />
      </div>
      <div
        class="grid-item grid-item-actions"
        style="grid-column: 2 / span 4; text-align: center"
      >
        <base-button mode="outline" @click="getImdb">Details</base-button>
        <base-button mode="outline" @click="downloadItem">Generate</base-button>
        <base-button :mode="!isSecretInputShow ? 'outline' : ''" @click="toggleSeceret">
          Enter Code</base-button
        >
      </div>
      <div
        class="grid-item grid-item-secret"
        style="grid-column: 2 / span 4"
        v-show="isSecretInputShow"
      >
        <div class="input-group">
          <input type="text" v-model.trim="secretInput" />
          <button type="button" @click="getDownloadLink">Download</button>
        </div>
      </div>
    </div>
  </li>
</template>

<script>
export default {
  props: ["rowId", "fileName", "size", "posterLink", "imdbId", "isHd", "downloadLink"],
  data() {
    return {
      isSecretInputShow: false,
      imdbDetail: null,
      secretInput: "",
    };
  },
  methods: {
    toggleSeceret() {
      this.isSecretInputShow = !this.isSecretInputShow;
    },
    async downloadItem() {
      try {
        await this.$store.dispatch("files/downloadFile", {
          rowId: this.rowId,
          downloadLink: this.downloadLink,
        });
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
    },
    async getImdb() {
      try {
        let myHeaders = this.$store.getters.headerWithToken;

        let requestOptions = {
          method: "GET",
          headers: myHeaders,
        };

        const response = await fetch(
          `${this.$store.state.auth.thanosUrl}/api/ThanosSnap/Rating?imdbID=${this.imdbId}`,
          requestOptions
        );
        const responseData = await response.json();

        if (!response.ok) {
          const error = new Error(
            responseData.message || "Failed to fetch in IMDB Rate!"
          );
          throw error;
        }
        this.imdbDetail = responseData.Data[0];
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
    },
    async getDownloadLink() {
      try {
        let myHeaders = this.$store.getters.headerWithToken;
        myHeaders.append("Content-Type", "application/json");
        var raw = JSON.stringify({
          secretCode: this.secretInput,
        });
        let requestOptions = {
          method: "POST",
          headers: myHeaders,
          body: raw,
        };

        const response = await fetch(
          `${this.$store.state.auth.thanosUrl}/api/ThanosSnap/DownloadLink`,
          requestOptions
        );
        const responseData = await response.json();

        if (!response.ok) {
          const error = new Error(
            responseData.message || "Failed to fetch in IMDB Rate!"
          );
          throw error;
        }
        console.log(responseData);
        this.downloadSource(responseData.DownloadURL);
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
    },
    downloadSource(source) {
      const fileName = this.fileName;
      var el = document.createElement("a");
      el.setAttribute("href", source);
      el.setAttribute("target", "_blank");
      el.setAttribute("download", fileName);
      document.body.appendChild(el);
      el.click();
      el.remove();
    },
  },
};
</script>

