<template>
  <div>
    <base-dialog :show="!!error" title="An error occurred!" @close="handleError">
      <p>{{ error }}</p>
    </base-dialog>
    <section>
      <files-filter @change-filter="setFilters"></files-filter>
    </section>
    <section>
      <base-card>
        <div class="controls" v-if="showList.length && !isSearchResult">
          <base-button mode="outline" @click="loadFileList(true)">Refresh</base-button>
          <div class="input-group">
            <input type="number" v-model.number="gotoInput" />
            <button type="button" @click="goToPage">Go</button>
          </div>
          <div>
            <base-button mode="outline" @click="prevPage" v-show="is_btn_prev_show"
              >Prev</base-button
            >
            <span class="page">{{ current_page }} / {{ totalPages }}</span>
            <base-button mode="outline" @click="nextPage" v-show="is_btn_next_show"
              >Next</base-button
            >
          </div>
        </div>
        <div v-if="isLoading">
          <base-spinner></base-spinner>
        </div>
        <ul v-if="showList.length">
          <file-item
            v-for="file in showList"
            :key="file.RowID"
            :row-id="file.RowID"
            :file-name="file.FileName"
            :size="file.Size"
            :poster-link="file.PosterLink"
            :imdb-id="file.imdbId"
            :is-hd="file.isHD"
            :download-link="file.DownloadLink"
          ></file-item>
        </ul>
        <h3 v-else>No Files found.</h3>
        <div class="controls" v-if="showList.length && !isSearchResult">
          <base-button mode="outline" @click="loadFileList(true)">Refresh</base-button>
          <div class="input-group">
            <input type="number" v-model.number="gotoInput" />
            <button type="button" @click="goToPage">Go</button>
          </div>
          <div>
            <base-button mode="outline" @click="prevPage" v-show="is_btn_prev_show"
              >Prev</base-button
            >
            <span class="page">{{ current_page }} / {{ totalPages }}</span>
            <base-button mode="outline" @click="nextPage" v-show="is_btn_next_show"
              >Next</base-button
            >
          </div>
        </div>
      </base-card>
    </section>
  </div>
</template>

<script>
import FileItem from "../../components/files/FilesItem.vue";
import FilesFilter from "../../components/files/FilesFilter.vue";

export default {
  components: {
    FileItem,
    FilesFilter,
  },
  data() {
    return {
      isLoading: false,
      error: null,
      totalPages: null,
      current_page: 1,
      records_per_page: 25,
      showList: [],
      is_btn_prev_show: false,
      is_btn_next_show: true,
      gotoInput: 1,
      isSearchResult: false,
      totalCount: 0,
    };
  },
  computed: {
    isLoggedIn() {
      return this.$store.getters.isAuthenticated;
    },
    filesList() {
      return this.$store.getters["files/fileList"];
    },
    startIndex() {
      const indx = (this.current_page - 1) * this.records_per_page;
      return 1 < indx ? 1 : indx;
    },
  },
  watch: {
    gotoInput(newVal) {
      if (this.totalPages < newVal) {
        this.gotoInput = this.totalPages;
      } else {
        this.gotoInput = newVal;
      }
    },
    filesList(newArr) {
      console.log("watch filesList");
      this.totalCount = newArr.length;
      this.totalPages = Math.ceil(this.totalCount / this.records_per_page);
      this.showList = [];
      for (
        var i = (this.current_page - 1) * this.records_per_page;
        i < this.current_page * this.records_per_page;
        i++
      ) {
        this.showList.push(this.filesList[i]);
      }
    },
  },
  created() {
    this.loadFileList();
  },
  methods: {
    async setFilters(filterInput) {
      if (filterInput.length < 1) {
        this.isSearchResult = false;
        this.current_page = 1;
        this.changePage(this.current_page);
        return;
      }
      this.isSearchResult = true;
      try {
        let myHeaders = this.$store.getters.headerWithToken;
        let requestOptions = {
          method: "GET",
          headers: myHeaders,
        };

        const response = await fetch(
          `${this.$store.state.auth.thanosUrl}/api/ThanosSnap/Search?keyword=${filterInput}`,
          requestOptions
        );
        const responseData = await response.json();

        if (!response.ok) {
          const error = new Error(
            responseData.message || "Failed to fetch in IMDB Rate!"
          );
          throw error;
        }
        this.showList = responseData.Data;
        console.log(this.showList);
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
    },
    async loadFileList(forceRefresh = false) {
      this.isLoading = true;
      try {
        await this.$store.dispatch("files/loadFileList", { forceRefresh });
      } catch (error) {
        this.error = error.message || "Something went wrong in Load File List!";
      }
      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    },
    goToPage() {
      this.current_page = this.gotoInput;
      this.changePage(this.current_page);
    },
    prevPage() {
      if (this.current_page > 1) {
        this.current_page--;
        this.changePage(this.current_page);
      }
    },
    nextPage() {
      if (this.current_page < this.numPages()) {
        this.current_page++;
        this.changePage(this.current_page);
      }
    },
    numPages() {
      return Math.ceil(this.totalCount / this.records_per_page);
    },
    changePage(page) {
      this.gotoInput = page;
      if (page < 1) page = 1;
      if (page > this.numPages()) page = this.numPages();

      this.showList = [];
      for (
        var i = (page - 1) * this.records_per_page;
        i < page * this.records_per_page;
        i++
      ) {
        this.showList.push(this.filesList[i]);
      }
      // page_span.innerHTML = page;

      if (page == 1) {
        this.is_btn_prev_show = false;
      } else {
        this.is_btn_prev_show = true;
      }

      if (page == this.numPages()) {
        this.is_btn_next_show = false;
      } else {
        this.is_btn_next_show = true;
      }
    },
  },
};
</script>

