export default {
    namespaced: true,
    state() {
        return {
            lastFetch: null,
            fileList: []
        };
    },
    getters: {
        fileList(state) {
            return state.fileList;
        },
        shouldUpdate(state) {
            const lastFetch = state.lastFetch;
            if (!lastFetch) {
                return true;
            }
            const currentTimeStamp = new Date().getTime();
            return (currentTimeStamp - lastFetch) / 1000 > 60;
        }
    },

    mutations: {
        setFileList(state, payload) {
            state.fileList = payload;
        },
        setFetchTimestamp(state) {
            state.lastFetch = new Date().getTime();
        },
        setLastFetchNull(state) {
            state.lastFetch = null;
        }
    },
    actions: {
        async loadFileList(context, payload) {
            if (!payload.forceRefresh && !context.getters.shouldUpdate) {
                console.log("load file list refused")
                return;
            }
            const start = 1;
            const limit = 100000;

            let myHeaders = context.rootGetters.headerWithToken
            let requestOptions = {
                method: 'GET',
                headers: myHeaders
            };
            console.log()
            const response = await fetch(
                `${context.rootState.auth.thanosUrl}/api/ThanosSnap/List?startIndex=${start}&limit=${limit}`, requestOptions
            );
            const responseData = await response.json();

            if (!response.ok) {
                const error = new Error(responseData.message || 'Failed to fetch!');
                throw error;
            }

            const fileList = [];

            console.log(responseData.Data)
            for (const key in responseData.Data) {
                let obj = responseData.Data[key]
                const file = {...obj }
                fileList.push(file);
            }

            context.commit('setFileList', fileList);
            context.commit('setFetchTimestamp');
        },
        async downloadFile(context, payload) {
            let myHeaders = context.rootGetters.headerWithToken;
            myHeaders.append("Content-Type", "application/json");
            var raw = JSON.stringify({
                "DownloadLink": payload.downloadLink,
                "RowID": payload.rowId
            });
            let requestOptions = {
                method: 'PUT',
                headers: myHeaders,
                body: raw
            };

            const response = await fetch(
                `${context.rootState.auth.thanosUrl}/api/ThanosSnap/SecretCode`, requestOptions
            );
            const responseData = await response.json();

            if (!response.ok) {
                const error = new Error(responseData.message || 'Failed to fetch in download file!');
                throw error;
            }
            // console.log(responseData)

        }
    },
};