<template>
  <div>
    <base-dialog :show="!!error" title="An error occurred" @close="handleError">
      <p>{{ error }}</p>
    </base-dialog>
    <base-dialog :show="isLoading" title="Authenticating..." fixed>
      <base-spinner></base-spinner>
    </base-dialog>
    <base-card>
      <form @submit.prevent="submitForm">
        <label for="username">Username</label>
        <div class="form-control">
          <div class="input-group" style="width: 100%">
            <input type="email" id="username" v-model.trim="username" />
            <button>Login</button>
          </div>
        </div>
        <p v-if="!formIsValid">
          Please enter a valid username and password (must be at least 6 characters long).
        </p>
      </form>
    </base-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      formIsValid: true,
      isLoading: false,
      error: null,
    };
  },
  created() {
    this.logout();
  },
  methods: {
    async submitForm() {
      this.formIsValid = true;
      if (this.username === "" || !this.username.includes("@")) {
        this.formIsValid = false;
        return;
      }

      this.isLoading = true;

      const actionPayload = {
        username: this.username,
      };

      try {
        await this.$store.dispatch("login", actionPayload);
        const redirectUrl = "/" + (this.$route.query.redirect || "files");
        this.$router.replace(redirectUrl);
      } catch (err) {
        this.error = err.message || "Failed to authenticate, try later.";
      }

      this.isLoading = false;
    },
    handleError() {
      this.error = null;
    },
    logout() {
      this.$store.dispatch("logout");
    },
  },
};
</script>

